md-autocomplete.md-default-theme , md-autocomplete {
  background: rgba(color($background), 1);
  &[disabled]:not([md-floating-label]) {
    background: rgba(color($background), 2);
  }
  button {
    md-icon {
      path {
        fill: color($background, "600");
      }
    }
    &:after {
      background: rgba(color($background, "600"), 0.3);
    }
  }
  input {
    color: color($foreground, "1");
  }
}
.md-autocomplete-suggestions-container.md-default-theme , .md-autocomplete-suggestions-container {
  background: rgba(color($background), 1);
  li {
    color: color($foreground, "1");
    &:hover,
    &.selected {
      background: rgba(color($background, "500"), 0.18);
    }
  }
}
