md-menu-content.md-default-theme , md-menu-content {
  background-color: rgba(color($background), 1);

  md-menu-item {
    color: color($foreground, "1");

    md-icon {
      color: color($foreground, "2");
    }

    .md-button[disabled] {
      color: color($foreground, "3");

      md-icon {
        color: color($foreground, "3");
      }
    }

  }

  md-menu-divider {
    background-color: color($foreground, "4");
  }
}
